<template>
  <div id='receive_money' class="card  card-container">
    <div class="card-body">
      <p>wait a minute please</p>
    </div>
    <ToaskMessage :content='this.errorContent' v-bind:shown='this.showCount'/>
  </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'ReceiveMoney',
    data () {
      return {
        errorContent: 'query data error',
        showCount: 0,
        money: 0,
        amountPerAge: 0,
        moneyCreationWay: 0
      }
    },
    methods: {
      toNext: function () {
        let me = this;
        if (this.moneyCreationWay === '0') {
          let user = me.$commonUtil.getUser();
          me.saveBidderMoney(user.id, function () {
            me.toNextPage();
          });
        } else {
          me.toNextPage();
        }
      },
      saveBidderMoney: function (bidderId, callback) {
        let me = this;
        let data = {};
        data.bidderId = bidderId;
        axios.post(
          '/api/bidderMoney/create',
          qs.stringify(data),
          {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
        ).then(result => {
          if (result.data.code !== 0) {
            me.errorContent = 'create money faile'
            me.showCount++;
          } else {
            callback();
          }
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'create money faile'
          me.showCount++;
        });
      },
      toNextPage: function () {
        let me = this;
        let nextPage = this.$commonUtil.getNextPageKey('receiveMoney', me.moneyCreationWay);
        router.replace({
          path: nextPage,
          query: {
            money: me.money,
            amountPerAge: me.amountPerAge
          }
        });
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    },
    mounted: function () {
      let me = this;
      this.money = me.$route.query.money;
      this.amountPerAge = me.$route.query.amountPerAge;
      this.moneyCreationWay = me.$route.query.moneyCreationWay;
      setTimeout(function () {
        me.toNext();
      }, 2000);
    }
  }
</script>

<style scoped>
</style>



